import { Value } from '@ali/4ever-cangjie';

export default function isSelectionInImage(value: Value) {
  const { selection, document } = value;

  return Boolean(
    selection &&
      selection.isCollapsed &&
      document.getClosest(
        selection.anchor.key,
        // @ts-ignore
        ({ type }) => type === 'image',
      ),
  );
}
